                <span class="detail_title1">ID</span>
                <span>{{waybillinfo.ID}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title1"></span>
                <span>
                  {{waybillinfo.ExcuteStatusName}}
                  <Button v-if="waybillinfo.ID!=null" icon='md-calendar' type="success" size='small' @click="logchange"></Button>
                </span>
              </li>
              <li class="itemli">
                <span class="detail_title1"></span>
                <span>{{waybillinfo.SourceName}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title1"></span>
                <span>{{waybillinfo.Merchandiser}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title1"></span>
                <span style="color:'#ccc';font-width:600">{{waybillinfo.chgTotalCurrency}} {{waybillinfo.chgTotalPrice}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title1">&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span style="color:'#ccc';font-width:600">CNY {{waybillinfo.total}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title1"></span>
                <span style="color:'#ccc';font-width:600" v-if="waybillinfo.overDue==true"></span>
                <span style="color:'#ccc';font-width:600" v-else></span>
              </li>
              <li class="itemli" v-if="waybillinfo.Condition!=undefined">
                <Icon type="md-alert" v-if="waybillinfo.Condition!=undefined" style="font-size: 22px;color: #da2828;" />
                <Tag color="geekblue" v-if="waybillinfo.Condition.AgencyCheck==true"></Tag>
                <Tag color="purple" v-if="waybillinfo.Condition.AgencyPayment==true"></Tag>
                <Tag color="blue" v-if="waybillinfo.Condition.AgencyReceive==true"></Tag>
                <Tag color="cyan" v-if="waybillinfo.Condition.ChangePackaging==true"></Tag>
                <Tag color="green" v-if="waybillinfo.Condition.LableServices==true"></Tag>
                <Tag color="gold" v-if="waybillinfo.Condition.PayForFreight==true"></Tag>
                <Tag color="orange" v-if="waybillinfo.Condition.Repackaging==true"></Tag>
                <Tag color="volcano" v-if="waybillinfo.Condition.UnBoxed==true"></Tag>
                <Tag color="red" v-if="waybillinfo.Condition.VacuumPackaging==true"></Tag>
                <Tag color="magenta" v-if="waybillinfo.Condition.WaterproofPackaging==true"></Tag>
                <Tag color="lime" v-if="waybillinfo.Condition.QualityInspection==true"></Tag>
                <Tag color="yellow" v-if="waybillinfo.Condition.Unboxing==true"></Tag>
                <Tag color="#FFA2D3" v-if="waybillinfo.Condition.IsTurnDeclare==true"></Tag>
              </li>
            </ul>
            </Col>
            <Col style="width: 24%;float: left;">
            <ul class="detail_li">
              <li class="itemli">
                <span class="detail_title2"></span>
                <span v-if="detailitem.length>0">{{detailitem[0].CreateDate|showDateexact}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title2" style="float: left;">&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span style="display: inline-block; width: 78%; line-height: 1;">
                  <p v-for="(item,index) in waybillinfo.Supplier">{{item.Supplier}}&nbsp;&nbsp;<Tag color="volcano">LV {{item.SupplierGrade}}</Tag></p>
                </span>
              </li>
              <li class="itemli">
                <span class="detail_title2"></span>
                <!-- <span>{{waybillinfo.TypeName}}</span> -->
                <span v-if="warehouseID.indexOf('SZ')!=-1||(waybillinfo.Type==1||waybillinfo.Type==2) ">{{waybillinfo.TypeName}}</span>
                <Select v-else
                        v-model="waybillinfo.Type"
                        style="width:40%"
                        @on-change='changewaybillType'>
                  <Option v-for="(item,index) in TypeArr"
                          :value="item.value"
                          :key="index">
                    {{ item.label}}
                  </Option>
                </Select>
              </li>
              <li class="itemli">
                <span class="detail_title2">&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span style="color:'#ccc';font-width:600">CNY {{waybillinfo.totalDebt}}</span>
              </li>
              <li class="itemli" v-if="DelivaryOpportunity!=null">
                <span class="detail_title2"></span>
                <span style="color:'#ccc';font-width:600">{{DelivaryOpportunity}}</span>
              </li>
              <li class="itemli" v-if="ispay!=null">
                <span class="detail_title2"></span>
                <span style="color:'#ccc';font-width:600">{{ispay}}</span>
              </li>
              <li class="itemli" v-if="waybillinfo.Type==1">
                <span class="detail_title2"></span>
                <span>{{waybillinfo. AppointTime|showDateexact}}</span>
              </li>
              <li class="itemli">
                <span class="detail_title2"></span>
                <span style="line-height: 20px;width: 70%;display: inline-block;">
