                    <span class="linkurlcolor Filesbox" @click="clackFilesProcess(item.Url)">{{item.CustomName}}</span>
                    <a @click="fileprint(item.Url)"></a>
                  </div>
                </span>
              </li>
            </ul>
            </Col>
            <Col style="width: 30%;float: left;">
            <ul class="detail_li">
