            // this.detailitem=res;
            if (res.length > 0) {
              var historydata = this.detailitem
              var newdata = []
              var result = new Array();
              var sameCount = 0;
              for (var i = 0; i < historydata.length; i++) {
                var tempA = historydata[i].ID;
                for (var j = 0; j < res.length; j++) {
                  var tempB = res[j].ID;
                  if (tempA == tempB) {
